import { Button, Card, Col, Form, Image, Input, Row, Typography } from "antd";
import Icon from "@ant-design/compatible/lib/icon";
import React, { useEffect, useState } from "react";
import { useRouter } from "next/router";
import styles from "../../styles/Login.module.css";
import { CLoadingSpinner, ErrorNotification, CTitle } from "@components";
import CApi from "@api";
import { useSelector } from "react-redux";

const { Title } = Typography;
const Login = () => {
  const { config } = useSelector((state) => state.config);
  const [isAuthenticated, SetIsAuthenticated] = useState(true);
  const router = useRouter();

  useEffect(() => {
    getMe();
  }, []);

  const [forms, setForms] = useState({
    email: "",
    password: "",
  });
  const [errors, setErrors] = useState({});

  const handleChange = (e) => {
    setForms({
      ...forms,
      [e.target.id]: e.target.value,
    });
  };

  const getMe = async () => {
    await CApi({
      method: "get",
      url: "api/me",
    })
      .then((_res) => {
        router.push("/");
      })
      .catch((_err) => {
        SetIsAuthenticated(false);
      });
  };
  const getCookie = async () => {
    await CApi({
      method: "GET",
      url: "sanctum/csrf-cookie",
    })
      .then((_res) => {
        handleLogin();
      })
      .catch((_err) => {});
  };
  const handleLogin = async () => {
    await CApi({
      method: "POST",
      url: "api/login",
      data: forms,
    })
      .then((_res) => {
        router.push("/");
      })
      .catch((err) => {
        if ("statusText" in err.response && "message" in err.response.data) {
          ErrorNotification(err.response.statusText, err.response.data.message);
        }
        if ("errors" in err.response.data) {
          setErrors(err.response.data.errors);
        }
      });
  };

  if (isAuthenticated) return <CLoadingSpinner />;

  return (
    <Row>
      <CTitle />
      <Col sm={{ span: 17 }} xs={{ span: 24 }}>
        <img
          src="/assets/bg.jpeg"
          className={styles.img}
          alt="Backgroud"
          layout="fill"
        />
      </Col>
      <Col
        sm={{ span: 7 }}
        xs={{ span: 24 }}
        style={{ backgroundColor: "#373737" }}
      >
        <Card className={styles.container}>
          <Col span={24}>
            <Col
              style={{
                marginTop: "7vh",
                textAlign: "center",
                marginBottom: "7vh",
              }}
            >
              <Image
                width={250}
                height={65}
                preview={false}
                src="/vercel.svg"
                alt="logo"
              />
            </Col>
            <Form
              style={{
                alignContent: "center",
                justifyContent: "center",
                alignItems: "center",
                justifyItems: "center",
              }}
              layout="vertical"
            >
              <Col
                style={{
                  textAlign: "center",
                  color: "black",
                  marginBottom: 10,
                }}
              >
                <Title style={{ marginBottom: -10, color: "#fff" }}>AMS</Title>
                <h2 style={{ fontWeight: "bold", color: "#fff" }}>
                  Asset Management System
                </h2>
              </Col>
              <Form.Item
                validateStatus={errors.email ? "error" : null}
                help={errors.email}
              >
                <Input
                  prefix={
                    <Icon type="mail" style={{ color: "rgba(41,42,43)" }} />
                  }
                  style={{ marginBottom: 10 }}
                  type="email"
                  placeholder="Email"
                  id="email"
                  name="email"
                  required="required"
                  value={forms.email}
                  onChange={handleChange}
                />
              </Form.Item>
              <Form.Item
                validateStatus={errors.password ? "error" : null}
                help={errors.password}
              >
                <Input.Password
                  style={{ marginBottom: 10 }}
                  prefix={
                    <Icon type="lock" style={{ color: "rgba(41,42,43)" }} />
                  }
                  placeholder="Password"
                  id="password"
                  value={forms.password}
                  onChange={handleChange}
                />
              </Form.Item>
              <Button
                type="primary"
                onClick={() => getCookie()}
                className="login-form-button"
                style={{ width: "100%" }}
              >
                Login
              </Button>
            </Form>
            <Col style={{ textAlign: "center", marginTop: 25 }}>
              <h3 style={{ fontWeight: "bold", color: "#fff" }}>
                <Icon type="copyright" /> {new Date().getFullYear()} -{" "}
                {config.app_title}
              </h3>
            </Col>
          </Col>
        </Card>
      </Col>
    </Row>
  );
};

export default Login;
